.itemList {
  max-height: 300px; /* Adjust the max-height as needed */
  overflow-y: auto; /* Add vertical scrollbar if content overflows */
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #575757;
  padding: 1rem;
  margin-bottom: 1rem; /* Add some margin between items */
}

.itemInfo {
  display: flex;
  align-items: center;
}

.itemImage img {
  max-width: 100px;
  max-height: 100px;
  margin-right: 10px;
}

.itemDetails h3 {
  margin: 0;
  font-size: 1.5rem;
}

.price {
  font-size: 1.25rem;
}

.itemActions {
  display: flex;
  align-items: center;
}

.quantity span {
  font-size: 1.25rem;
  font-weight: bold;
}

.actions button {
  background-color: transparent;
  border: 1px solid white;
  margin-left: 0.5rem;
  padding: 0.15rem 1rem;
  color: white;
}

.actions button:hover,
.actions button:active {
  background-color: #4b4b4b;
  color: white;
}
